import React from 'react';
import { useSelector } from '../../services/hooks';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

import styles from './constructor-page.module.css';

import BurgerIngredients from '../../components/burger-ingredients/burger-ingredients';
import BurgerConstructor from '../../components/burger-constructor/burger-constructor';

export default function ConstructorPage() {

  const {isLoading, isFailed} = useSelector(store => store.allIngredients);
  const listOfIngredients = useSelector(store => store.allIngredients.items);

  return (
    <main className={styles.main}>
      <DndProvider backend={HTML5Backend}>

      {
        isLoading && <p className="text text_type_main-medium"> ...</p>
      }
      {
        isFailed && <p className="text text_type_main-medium">   . ,  </p>
      }
      {
        listOfIngredients &&
        <>
          <BurgerIngredients />
          <BurgerConstructor />
        </>
      }
      </DndProvider>
    </main>
  )
}
